body {
  background-color: #5882cf;
  font-family: Franklin Gothic Medium, Arial Narrow, Arial, sans-serif;
}

.cat-info {
  align-items: center;
  justify-items: center;
  margin: 40px 0;
  display: grid;
}

.cat-cart {
  height: 400px;
  border-radius: 5px;
  box-shadow: 0 0 50px 15px #51e61f;
}

.cat-name, .cat-description, .cat-temperament {
  max-width: 800px;
}

.cat-temperament {
  font-size: 20px;
}

.cat-span {
  text-transform: uppercase;
  color: #e60f21;
  font-size: 25px;
}

.cat-name, .error {
  text-transform: uppercase;
  color: #d10a92;
}

.error {
  justify-content: center;
  display: flex;
  position: relative;
  top: -90px;
}

.loader-container {
  justify-content: center;
  align-items: center;
  margin-top: 35px;
  display: flex;
}

.loader {
  width: 100px;
  height: 150px;
  justify-content: center;
  align-items: center;
  animation: heartBeat 1.2s cubic-bezier(.215, .61, .355, 1) infinite;
  position: relative;
}

.loader:before, .loader:after {
  content: "";
  width: 100px;
  height: 150px;
  transform-origin: 50% 68%;
  background: #ff3d00;
  border-radius: 50px 50px 0 0;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: rotate(45deg);
  box-shadow: inset 5px 4px 5px rgba(0, 0, 0, .267);
}

.loader:after {
  transform: rotate(-45deg);
}

@keyframes heartBeat {
  0% {
    transform: scale(.95);
  }

  5% {
    transform: scale(1.1);
  }

  39% {
    transform: scale(.85);
  }

  45% {
    transform: scale(1);
  }

  60% {
    transform: scale(.95);
  }

  100% {
    transform: scale(.9);
  }
}

.is-hidden {
  display: none;
}

